import React, {Component} from 'react'
import {Redirect } from 'react-router-dom'
 import {connect} from 'react-redux'
import Input from '../../components/UI/Input/Input'
import Button from '../../components/UI/Button/Button'
import Spinner from '../../components/UI/Spinner/Spinner'
import classes from './Auth.css'
import * as actions from '../../store/action/index'
import {updateObject,checkValidation} from '../../shared/Utility'

class Auth extends Component {

    state = {
        controls : {
            email: {
                elementType : 'input',
                elementConfig: {
                    type: 'email',
                    placeholder:'Mail Address'
                },
                value : '',
                validation: {
                    required:true,
                    isEmail: true
                },
                touched: false
            },
            password: {
                elementType : 'input',
                elementConfig: {
                    type: 'password',
                    placeholder:'Password'
                },
                value : '',
                validation: {
                    required:true,
                    minLength: 8
                },
                touched: false
            }
        },
        isSignUp : true
    }

     
    componentDidMount(){
        if(!this.props.buildBurger && this.props.authRedirectPath !== '/'){
            this.props.onSetRedirectPath()
        }
    }

    inputChangeHandler = (event,controlName) => {
        const updateControls = updateObject(this.state.controls,{
            [controlName] : updateObject(this.state.controls[controlName],{
                value:event.target.value,
                valid: checkValidation(event.target.value,this.state.controls[controlName].validation),
                touched:true
            }) 
        })
        this.setState({controls:updateControls})
    }

    switchAuthModeHandler = () => {
        this.setState (prevState => {
            return {isSignUp : !prevState.isSignUp}
        })
    }

    submitHandler = (event) => {
        event.preventDefault()
        this.props.onAuthentication(this.state.controls.email.value,this.state.controls.password.value,this.state.isSignUp)
    }
    render () {
        let formElementsArray = [];
        for(let key in this.state.controls){
            formElementsArray.push({
                id:key,
                config:this.state.controls[key]
            })
        }

        let form = formElementsArray.map((formElement) => {
            return <Input 
                        key={formElement.id}
                        elementType={formElement.config.elementType} 
                        elementConfig={formElement.config.elementConfig} 
                        value={formElement.config.value} 
                        invalid={!formElement.config.valid}
                        shouldValidate={formElement.config.validation}
                        touched={formElement.config.touched}
                        changed={(event)=>this.inputChangeHandler(event,formElement.id)} />
        })

        if(this.props.loading) {
            form = <Spinner />
        }

        let errorMessage = null;

        if(this.props.error) {
            errorMessage = this.props.error.message
        }
        let authRedirect = null;
        if(this.props.isAuthenticate) {
            authRedirect = (<Redirect to={this.props.authRedirectPath} />)
        }
        return(
            <div className={classes.Auth}>
                {authRedirect}
                {errorMessage}
                <form onSubmit={this.submitHandler}>
                    {form}
                    <Button buttonType="Success">Sumbit</Button>
                </form>
                <Button clicked={this.switchAuthModeHandler} buttonType="Danger">SWITCH TO {this.state.isSignUp ? 'SIGNIN' : 'SIGNUP'}</Button>
            </div>
        );
    }
}
const mapStateToProps = state => {
    return {
        loading : state.auth.loading,
        error : state.auth.error,
        isAuthenticate : state.auth.tokenId !== null,
        buildBurger : state.burgerBuilder.building,
        authRedirectPath : state.auth.authRedirectPath
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onAuthentication:(email, password, isSignUp) => dispatch(actions.auth(email, password, isSignUp)),
        onSetRedirectPath:() => dispatch(actions.setAuthRedirectPath('/'))
    }
}



export default connect(mapStateToProps,mapDispatchToProps)(Auth)


